import * as React from 'react';
import { useState, useEffect } from 'react';
import Layout from '../../components/Layout';
import Box from '@mui/material/Box';
import Typography from '@mui/material/Typography';
import TextField from '@mui/material/TextField';
import Button from '@mui/material/Button';
import CircularProgress from '@mui/material/CircularProgress';
import Parse from 'parse/dist/parse.min.js';
import { Link } from 'gatsby';
import Seo from '../../components/seo';
import { LiveProvider, LiveEditor, LiveError, LivePreview } from 'react-live';

const APP_ID = '3ZraCGVEMT8vTcJXzKKiZ65OQ9dsJJqo3wDgneHM';
const JS_KEY = 'hQkocwX7BFcwJ5aOYqE7rtbmBqRxeIw9aiLuf7Cp';

Parse.initialize(APP_ID, JS_KEY);
Parse.serverURL = 'https://mobile413.b4a.io';

const AppPage = ({ params }) => {
    const { placeholder } = params;
    const [prompt, setPrompt] = useState('');
    const [isLoading, setIsLoading] = useState(false);
    const [componentCode, setComponentCode] = useState('');

    const callGetApp = async (input) => {
        setIsLoading(true);

        try {
            // Call Parse Cloud function "getApp"
            const result = await Parse.Cloud.run('getApp', { prompt: input });
            console.log('Cloud function result:', result);

            // Set the component code returned by the backend
            setComponentCode(`



() => {
  const [input, setInput] = React.useState('');
  const [result, setResult] = React.useState('');

  const handleClick = (value) => {
    if (value === '=') {
      try {
        setResult(eval(input).toString());
      } catch (error) {
        setResult('Error');
      }
    } else if (value === 'C') {
      setInput('');
      setResult('');
    } else {
      setInput((prevInput) => prevInput + value);
    }
  };

  return (
    <div className="calculator">
      <div className="display">
        <input type="text" value={input} readOnly />
        <div className="result">{result}</div>
      </div>
      <div className="buttons">
        <button onClick={() => handleClick('7')}>7</button>
        <button onClick={() => handleClick('8')}>8</button>
        <button onClick={() => handleClick('9')}>9</button>
        <button onClick={() => handleClick('/')}>/</button>

        <button onClick={() => handleClick('4')}>4</button>
        <button onClick={() => handleClick('5')}>5</button>
        <button onClick={() => handleClick('6')}>6</button>
        <button onClick={() => handleClick('*')}>*</button>

        <button onClick={() => handleClick('1')}>1</button>
        <button onClick={() => handleClick('2')}>2</button>
        <button onClick={() => handleClick('3')}>3</button>
        <button onClick={() => handleClick('-')}>-</button>

        <button onClick={() => handleClick('0')}>0</button>
        <button onClick={() => handleClick('.')}>.</button>
        <button onClick={() => handleClick('=')}>=</button>
        <button onClick={() => handleClick('+')}>+</button>

        <button onClick={() => handleClick('C')} className="clear">C</button>
      </div>
    </div>
  );
};

`);
        } catch (error) {
            console.error('Error calling cloud function:', error);
            alert('Failed to call cloud function. Please try again.');
        } finally {
            setIsLoading(false);
        }
    };

    // Run getApp on page load with the placeholder as the prompt
    useEffect(() => {
        callGetApp(placeholder);
    }, [placeholder]);

    return (
        <Layout>
            <Box
                sx={{
                    display: 'flex',
                    flexDirection: 'column',
                    alignItems: 'center',
                    justifyContent: 'center',
                    minHeight: '80vh',
                    textAlign: 'center',
                }}
            >
                {/* App Name */}
                <Typography variant="h3" component="h1" gutterBottom>
                    App Name: {placeholder}
                </Typography>

                {/* Prompt Textbox */}
                <TextField
                    label="Enter your prompt"
                    variant="outlined"
                    value={prompt}
                    onChange={(e) => setPrompt(e.target.value)}
                    sx={{ width: '300px', mb: 2 }}
                />

                {/* Work on it Button */}
                <Button
                    variant="contained"
                    color="primary"
                    size="large"
                    onClick={() => callGetApp(prompt)} // Call getApp with the user-entered prompt
                    disabled={isLoading} // Disable button while loading
                >
                    {isLoading ? <CircularProgress size={24} /> : 'Work on it'}
                </Button>

                {/* Dynamically Loaded Component */}
                {componentCode && (
                    <LiveProvider
                        code={componentCode}
                        scope={{ React, Link, Layout, Seo }}
                    >
                        <LivePreview />
                        <LiveEditor />
                        <LiveError />
                        
                    </LiveProvider>
                )}
            </Box>
        </Layout>
    );
};

export default AppPage;